body {
  font-family: 'open sans', sans-serif;
  font-size: 16px;
}

.App {
  margin-left: 10%;
  margin-right: 10%;
  margin-top: 1%;
}

table {
  width: 100%;
  border-collapse: collapse;
}

thead th {
  text-align: right;
  border-bottom: 1px solid #ccc;
}

thead button {
  background-color: white;
}

thead th:first-child {
  text-align: left;
}

thead button {
  border: 0;
  border-radius: none;
  font-family: inherit;
  font-size: inherit;
  padding: 0.5em;
  margin-bottom: 1px;
}

thead button.ascending::after {
  content: '⬇';
  display: inline-block;
  margin-left: 1em;
}

thead button.descending::after {
  content: '⬆';
  display: inline-block;
  margin-left: 1em;
}

tbody td {
  padding: 0.5em;
  text-align: right;
  height: 51px;
  font-weight: 600;
  white-space: nowrap;
}

tbody tr {
  border-bottom: 1px solid #ccc;
}

tbody td img {
  width: 30px;
  height: 30px;
  border-radius: 15px;
}

tbody td:first-child {
  text-align: left;
  display: flex;
  align-items: center;
}

tbody td:first-child span {
  padding-left: 20px;
}
/* 
tbody td:nth-child(3) {
  display: flex;
  align-items: center;
} */

tbody td:first-child span {
  padding-left: 20px;
}

tbody tr:hover {
  background-color: #eee;
}

.green-text {
  color: rgb(120 255 214);
  /* font-weight: bold; */
  font-size: 90%;
}

.red-text {
  color: rgb(244 91 105);
  font-size: 90%;
}

.grey-text {
  color: grey;
  font-size: 90%;
}